
*{
  margin: 0;
  padding: 0;
}
html,body {
  height: 100%;
  width: 100%;
  background: #ddd;
}
body{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
.inputbox{
  width: 100%;
  height: 30px;
  margin-bottom: 20px;
}
.lis{
  cursor: pointer;
}
ul{
  text-align: left;
  list-style: none;
  line-height: 60px;
  display: block;
}
li{
  font-size: 20px;
  font-weight: bold;
  display: block;
  height: 40px;
  line-height: 40px;
}
.del{
  display: none ;
}
h3{
  display: inline-block;
}
h1{
  width: 100%;
  overflow: auto;
  margin-top: 10px;
  margin-bottom: 20px;
}
li:hover .del{
display: inline-block;
font-size: 15px;
cursor: pointer;
}
#app .done{
  display: block;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.newn{
  display: inline-block;
  width: 70%;
  height: 100%;
}
.hel{
  display: none;
}
.dided{
  display: none;
}
.item-status{
  display: inline;
  background: red;
  color: white;
  padding: 0 5px;
  font-size: 12px;
}
.finish{
  text-decoration: line-through;
}
#sys{
  margin-bottom: 5px;
  width: 150px;
  height: 30px;
  font-size: 20px;
}



